.header {
    background-color: var(--primario);
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 4rem;
    position: sticky;
    top: 0;
    z-index: 999;
    width: 100%;
    border-bottom: .1rem solid var(--blanco);
}

.contenido {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    color: var(--blanco);
    cursor: pointer;
    transition: 0.3s;
}

.contenido:hover {
    color: var(--tercero);
    border-radius: .3rem;
}

.menu-vertical {
    display: none;
    box-shadow: 2px 2px 4px var(--primario);
    padding: .1rem;

    transition: 1s ease-in-out;
}

.contenido:hover .menu-vertical {
    display: block;
}

.header-submenu {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding-right: 1rem;
    gap: 1rem;
}

.header .logo {
    cursor: pointer;
}

.header .logo img {
    height: 3rem;
    width: auto;
    fill: var(--blanco);
    margin: 0rem .5rem;
}

.item-menu {
    padding: .3rem .5rem;
    border-radius: .7rem;
    font-size: 1.2rem;
    color: var(--primario);
    transition: 0.3s;
    cursor: pointer;
}

.item-menu:hover {
    color: var(--blanco);
    background-color: var(--primario);
}

.menu-vertical {
    position: absolute;
    display: none;
    width: 20rem;
    height: 40rem;
    overflow: auto;
    padding: .1rem;
    scrollbar-width: thin;
    scrollbar-color: transparent transparent;
    border-radius: 1rem;
    background-color: var(--blanco);
    top: 0;
    right: 0;
    transform: translateY(3.1rem) translateX(-5rem);
    border: 3px solid var(--primario);
    transition: 1s ease-in-out;
}

.menu-vertical::-webkit-scrollbar {
    width: .8rem;
    background-color: var(--tercero);
    border-radius: 2rem;
    transform: scale(.95);
}

.menu-vertical::-webkit-scrollbar-thumb {
    background-color: var(--primario);
    border-radius: 2rem;
    
    border: 1px solid var(--blanco);
}

.contacto {
    font-size: 1.3rem;
    width: 5rem;
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--negro);
    padding: .2rem .8rem;
    background-color: var(--blanco);
    border-radius: .3rem;
    cursor: pointer;
    transition: 0.3s;
}

.contacto:hover {
    background-color: var(--tercero);
    color: var(--negro);
    font-weight: bold;
}

.icon-menu {
    width: 1.5rem;
    height: auto;
    margin: .3rem;
}

@media only screen and (max-width: 820px) {
    .menu-vertical {
        width: 22rem;
        height: auto;
        transform: translateY(3.5rem) translateX(0rem);
    }

    /*.header .logo img {
        height: 3rem;
    }*/

    .contacto {
        display: none;
    }

    /*
    .contenido {
        font-size: 1.5rem;
    }

    .item-menu {

        box-shadow: 1px 1px 1px var(--primario);
    }*/
}

@media only screen and (max-width: 420px) {
    .menu-vertical {
        width: 18rem;
        height: 35rem;
        transform: translateY(3.5rem) translateX(0rem);
    }

    .header .logo img {
        display: none;
    }
    .header .logo-show {
        display: block;
    }
    .header .logo-show img {
        height: 4rem;
        width: auto;
        margin: 0rem;
    }
    /*
    .contenido {
