.contenedor {
    width: 100%;
    height: auto;
    background-color: var(--blanco);
    /*alinear los items de manera vertical y centrados*/
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.sub-menus {
    flex-direction: row;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: .5rem 1rem;
}

.sub-menu {
    background-color: var(--blanco);
    border-radius: 1rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    /*flex: 1 0 23rem;*/
    width: 28rem;
    height: 13rem;
    margin: .8rem;
    border: .1rem solid var(--primario);
    box-shadow: 2px 2px 4px var(--primario);
    transition: 0.3s;
    cursor: pointer;
    font-size: 100%;
    overflow: hidden;
}

.sub-menu:hover {
    transform: scale(1.02);
}

.encabezado {
    font-size: 1.4rem;
    font-weight: bold;
    color: var(--primario);
}

.leyenda {
    font-size: 1rem;
    color: var(--secundario);
    text-align: left;
    /*display: none;*/
}

.imagen-icono {
    height: 7rem;
    margin: .5rem;
    width: auto;
}

@media only screen and (max-width: 820px) {
    .imagen-icono {
        height: auto;
        width: 5rem;
        margin: 0rem;
    }

    .sub-menu {
        width: 45%;
    }

    .encabezado {
        font-size: 1.5em;
        font-weight: bold;
        color: var(--primario);
    }

    .leyenda {
        font-size: 1rem;
        color: var(--secundario);
    }
}

@media only screen and (max-width: 420px) {

    .sub-menu {
        width: 100%;
        height: auto;
        margin: .5rem;
        padding: 1.5rem .25rem;
    }

    .encabezado {
        font-size: 1.2em;
        font-weight: bold;
        color: var(--primario);
    }

    .leyenda {
        font-size: .8rem;
        color: var(--secundario);
    }
}