.principal {
    padding-top: 1rem;
    background-color: var(--blanco);
    /*alinear los items de manera vertical y centrados*/
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.titulo-main {
    font-size: 2.6rem;
    font-weight: bold;
    color: var(--negro);
    text-align: center;
    margin: .5rem;
}

.subtitulo-main {
    font-size: 2.4rem;
    font-weight: normal;
    color: var(--primario);
    text-align: center;
    margin: .5rem;
}

.main-mapa-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: var(--primario);
    width: 95%;
    height: auto;
    padding: 1rem 0rem;
    border-radius: 10rem;
}

.main-mapa-info {
    width: 30%;
    margin-right: 1rem;
}

.main-mapa-espace {
    border-radius: 1rem;
    position: relative;
    height: auto;
    overflow: hidden;
    cursor: pointer;
    transition: 0.3s;
    overflow: hidden;
    width: 50%;
}

.main-mapa-titulo {
    font-size: 2.5rem;
    color: var(--blanco);
    font-weight: bold;
}

.main-mapa-subtitulo {
    font-size: 1.5rem;
    color: var(--tercero);
}

.mapa-prueba {
    width: 100%;
    height: auto;
    background-position: center;
    background-attachment: fixed;
    cursor: pointer;
    filter: brightness(90%);
    border-radius: 1rem;
    border: .1rem solid var(--blanco);
    box-shadow: 2px 2px 4px var(--primario);
}

.mapa-prueba:hover {
    content: "Click para ver mapa";
    filter: brightness(60%);
    font-size: 1rem;
}

.contenedor-img {
    display: flex;
    justify-content: center;
    align-items: center;
}

.contenedor-img .texto-img {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    position: absolute;
    visibility: hidden;
    font-weight: bold;
    color: #ffffff;
    padding: 1rem;
    font-size: 1.4rem;
    transition: visibility 0s, opacity 0.5s ease-in-out;
    font-weight: bold;

    border-radius: 1rem;
}

.main-mapa-espace:hover .texto-img {
    visibility: visible;
    opacity: 1;
    background-color: rgba(255, 255, 255, 0);
    color: var(--blanco);
}

.main-mapa-espace:hover .mapa-prueba {
    opacity: .5;
    filter: blur(1px);
}

@media only screen and (max-width: 820px) {
    .titulo-main {
        font-size: 2rem;
        margin-top: 0rem;
    }

    .subtitulo-main {
        font-size: 1.6rem;
        margin: .5rem;
    }

    .main-mapa-container {
        flex-direction: column-reverse;
        width: 98%;
        height: auto;
        padding: 1rem 0rem;
        border-radius: 3rem;
    }

    .main-mapa-espace {
        border-radius: 2rem;
        width: 90%;
        height: auto;
    }

    .main-mapa-info {
        margin: 1rem 0rem;
        width: 85%;
    }

    .main-mapa-titulo {
        font-size: 2rem;
        color: var(--blanco);
        font-weight: bold;
        text-align: center;
    }

    .main-mapa-subtitulo {
        font-size: 1.3rem;
        color: var(--tercero);
        text-align: center;
    }
}

@media only screen and (max-width: 420px) {
    .titulo-main {
        font-size: 1.2rem;
    }

    .subtitulo-main {
        font-size: 1rem;
    }

    .main-mapa-container {
        flex-direction: column-reverse;
        width: 98%;
        height: auto;
        padding: 1rem 0rem;
        border-radius: 2rem;
    }

    .main-mapa-espace {
        border-radius: 1rem;
        width: 90%;
        height: 10rem;
    }

    .main-mapa-info {
        margin: .5rem 0rem;
        width: 90%;
    }

    .main-mapa-titulo {
        font-size: 1.2rem;
        color: var(--blanco);
        font-weight: bold;
        text-align: center;
    }

    .main-mapa-subtitulo {
        font-size: 1rem;
        color: var(--tercero);
        text-align: center;
    }
}