.main-biodiversidad {
    padding-top: 1rem;
    background-color: var(--blanco);
    /*alinear los items de manera vertical y centrados*/
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 1rem;
}

.subtitulo-main-biodiversidad {
    font-size: 2.5rem;
    font-weight: bold;
    color: var(--primario);
    margin: 1rem;
}
.subtitulo-main-biodiversidad-2 {
    font-size: 2rem;
    font-weight: bold;
    color: var(--negro);
    margin: 1rem;
}
.biodiversidad-container-videos{
    display: flex;
    flex-direction: row;
    margin-bottom: 2rem;
    gap: 1rem;
}
.video {
    border-radius: 1rem;
    
}