.image-gallery {
    margin: 0rem;
    background-color: var(--balnco);
    width: 100%;
    height: auto;
    padding-bottom: 1rem;
}

.image-gallery-title {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 1.5rem;
    color: var(--negro);
}

.image-gallery-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: row;
    gap: 2rem 1rem;
}

.image-gallery-item {
    width: 25rem;
    height: 20rem;
}

.image-gallery-item img {
    width: 100%;
    height: 100%;
    border-radius: .5rem;
    transition: .3s;
    box-shadow: 2px 2px 4px var(--negro);
}

.image-gallery-item img:hover{
    transform: scale(1.05);
}