.main-baner-texto {
    width: 100%;
    height: auto;
    background-color: var(--secundario);
    margin:0rem;
    padding-bottom: 3rem;
    /*alinear los items de manera vertical y centrados*/
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.titulo-baner-texto{
    text-align: center;
    font-size: 1.5rem;
    color: var(--blanco);
}
.parrafo-baner-texto{
    font-size: 1.1rem;
    text-align: left;
    color: var(--blanco);
    padding-left: 12rem;
    padding-right: 12rem;
    text-align: justify;
}
@media only screen and (max-width: 820px) {
    .titulo-baner-texto{
        font-size: 1.4rem;
    }
    .parrafo-baner-texto{
        font-size: 1rem;
        padding-left: 5rem;
        padding-right: 5rem;
    }

}

@media only screen and (max-width: 420px) {
    .titulo-baner-texto{
        font-size: 1.4rem;
    }
    .parrafo-baner-texto{
        font-size: .9rem;
        padding-left: 1rem;
        padding-right: 1rem;
    }
}