/* docLinks.css */

.main-docLinks {
    width: 100%;
    height: auto;
    background-color: var(--secundario);
    /*alinear los items de manera vertical y centrados*/
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.docLinks-title{
    
    text-align: center;
    font-size: 2rem;
    margin-bottom: 1.5rem;
    color: var(--blanco);
}
.card-doc {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--blanco);
    padding: 1rem;
    border-radius: 1rem;
    box-shadow: 2px 2px 4px var(--negro);
    margin-bottom: 1rem;
    width: 50rem;
}
.titulos h3 {
    font-size: 1.2rem;
    font-weight: bold;
}
.titulo-docLinks {
    font-size: 2rem;
    font-weight: bold;
    margin: 1rem;
    text-align: center;
    color: var(--blanco);
}

.titulos {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 90%;
    justify-content: space-between;
}
.contenedor-link {
    border: 1px solid var(--secundario);
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 90%;
    padding: .5rem;
    border-radius: .5rem;
    margin: .5rem;
    box-shadow: 2px 2px 4px var(--negro);
    justify-content: space-between;
    transition: .3s ease-in-out;
}
.contenedor-link:hover {
    transform: scale(1.01);
}
.contenedor-link:hover  .botton-docLinks {
    color: var(--negro);
}
.botton-docLinks {
    color: var(--secundario);
    font-weight: bold;
    transition: .3s ease-in-out;
}

.a-docLinks {
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: .3s;
}
.a-docLinks:hover {
    transform: scale(1.2);
}
.a-docLinks img {
    width: 2.5rem;
    height: auto;
}